            <span>%</span>
          </div>

          <div class="setting-group" v-if="resizeMode === 'fixed'">
            <label>ç›®æ ‡å°ºå¯¸ï¼š</label>
            <div class="size-inputs">
              <el-input-number v-model="targetWidth" :min="1" :max="5000" size="small"></el-input-number>
              <span>Ã—</span>
              <el-input-number v-model="targetHeight" :min="1" :max="5000" size="small"></el-input-number>
              <el-checkbox v-model="keepAspectRatio">ä¿æŒæ¯”ä¾‹</el-checkbox>
            </div>
          </div>

          <div class="setting-group" v-if="resizeMode === 'width'">
            <label>ç›®æ ‡å®½åº¦ï¼š</label>
            <el-input-number v-model="targetWidth" :min="1" :max="5000" size="small"></el-input-number>
            <span>px</span>
          </div>

          <div class="setting-group" v-if="resizeMode === 'height'">
            <label>ç›®æ ‡é«˜åº¦ï¼š</label>
            <el-input-number v-model="targetHeight" :min="1" :max="5000" size="small"></el-input-number>
            <span>px</span>
          </div>

          <div class="setting-group">
            <label>è¾“å‡ºè´¨é‡ï¼š</label>
            <el-slider v-model="quality" :min="10" :max="100" :step="5" show-input></el-slider>
            <span>%</span>
          </div>

          <div class="setting-group">
            <label>è¾“å‡ºæ ¼å¼ï¼š</label>
            <el-select v-model="outputFormat" size="small">
              <el-option label="ä¿æŒåŸæ ¼å¼" value="original"></el-option>
              <el-option label="JPEG" value="jpeg"></el-option>
              <el-option label="PNG" value="png"></el-option>
              <el-option label="WebP" value="webp"></el-option>
            </el-select>
