          </el-button>
          <el-button @click="clearImages">
            <i class="el-icon-delete"></i> æ¸…ç©ºå›¾ç‰‡
          </el-button>
          <el-button v-if="processedImages.length > 0" type="success" @click="downloadAll">
            <i class="el-icon-download"></i> ä¸‹è½½å…¨éƒ¨
          </el-button>
        </div>
