                  <p>{{ image.width }}Ã—{{ image.height }}</p>
                  <p>{{ formatFileSize(image.size) }}</p>
                </div>
              </div>
            </div>
            
            <div class="image-actions">
              <el-button size="mini" @click="removeImage(index)">åˆ é™¤</el-button>
              <el-button v-if="processedImages[index]" size="mini" type="success" @click="downloadSingle(index)">
                ä¸‹è½½
              </el-button>
            </div>

            <div v-if="processedImages[index]" class="processed-preview">
              <h4>å¤„ç†ç»“æœ</h4>
              <img :src="processedImages[index].url" alt="å¤„ç†ç»“æœ" />
              <div class="processed-info">
                <p>{{ processedImages[index].width }}Ã—{{ processedImages[index].height }}</p>
                <p>{{ formatFileSize(processedImages[index].size) }}</p>
                <p class="size-change">
                  {{ getSizeChangeText(image.size, processedImages[index].size) }}
                </p>
              </div>
