          <ul>
            <li>âœ… æ‰¹é‡è°ƒæ•´å›¾ç‰‡å°ºå¯¸</li>
            <li>âœ… å¤šç§è°ƒæ•´æ¨¡å¼</li>
            <li>âœ… è´¨é‡æ§åˆ¶</li>
            <li>âœ… æ ¼å¼è½¬æ¢</li>
            <li>âœ… å®æ—¶é¢„è§ˆ</li>
          </ul>
        </div>
      </div>
    </div>

    <div v-if="processing" class="processing-overlay">
      <div class="processing-content">
        <el-progress :percentage="progress" :stroke-width="8"></el-progress>
        <p>{{ progressText }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImageResizerView',
  data() {
    return {
      images: [],
      processedImages: [],
      resizeMode: 'percentage',
      percentage: 50,
      targetWidth: 800,
      targetHeight: 600,
      keepAspectRatio: true,
      quality: 80,
      outputFormat: 'original',
      processing: false,
      progress: 0,
      progressText: ''
    }
  },
  methods: {
    async handleFileChange(file) {
      if (!file.raw) return
      
      if (!file.raw.type.startsWith('image/')) {
        this.$message.error('è¯·é€‰æ‹©å›¾ç‰‡æ–‡ä»¶ï¼')
        return
      }

      if (file.raw.size > 10 * 1024 * 1024) {
        this.$message.error('å›¾ç‰‡æ–‡ä»¶ä¸èƒ½è¶…è¿‡10MBï¼')
        return
      }

      try {
        const imageData = await this.loadImage(file.raw)
        this.images.push(imageData)
        this.processedImages.push(null)
        this.$message.success(`å·²æ·»åŠ å›¾ç‰‡ï¼š${file.name}`)
      } catch (error) {
        console.error('åŠ è½½å›¾ç‰‡å¤±è´¥:', error)
        this.$message.error('å›¾ç‰‡åŠ è½½å¤±è´¥ï¼')
      }
    },

    loadImage(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onload = (e) => {
          const img = new Image()
          img.onload = () => {
            resolve({
              name: file.name,
              url: e.target.result,
              width: img.width,
              height: img.height,
              size: file.size,
              file: file,
              element: img
            })
          }
          img.onerror = reject
          img.src = e.target.result
        }
        reader.onerror = reject
        reader.readAsDataURL(file)
      })
    },

    removeImage(index) {
      this.images.splice(index, 1)
      this.processedImages.splice(index, 1)
    },

    clearImages() {
      this.images = []
      this.processedImages = []
    },

    async processImages() {
      if (this.images.length === 0) return

      this.processing = true
      this.progress = 0
      this.progressText = 'å¼€å§‹å¤„ç†å›¾ç‰‡...'

      try {
        for (let i = 0; i < this.images.length; i++) {
          this.progressText = `æ­£åœ¨å¤„ç†ç¬¬ ${i + 1} å¼ å›¾ç‰‡...`
          this.progress = (i / this.images.length) * 100

          const processed = await this.resizeImage(this.images[i])
          this.$set(this.processedImages, i, processed)
        }

        this.progress = 100
        this.progressText = 'å¤„ç†å®Œæˆï¼'
        
        setTimeout(() => {
          this.processing = false
          this.$message.success('æ‰€æœ‰å›¾ç‰‡å¤„ç†å®Œæˆï¼')
        }, 500)

      } catch (error) {
        console.error('å¤„ç†å¤±è´¥:', error)
        this.processing = false
        this.$message.error('å›¾ç‰‡å¤„ç†å¤±è´¥ï¼š' + error.message)
      }
    },

    async resizeImage(imageData) {
      return new Promise((resolve) => {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        
        let newWidth, newHeight
        
        switch (this.resizeMode) {
          case 'percentage':
            newWidth = Math.round(imageData.width * this.percentage / 100)
            newHeight = Math.round(imageData.height * this.percentage / 100)
            break
          case 'fixed':
            if (this.keepAspectRatio) {
              const ratio = Math.min(this.targetWidth / imageData.width, this.targetHeight / imageData.height)
              newWidth = Math.round(imageData.width * ratio)
              newHeight = Math.round(imageData.height * ratio)
            } else {
              newWidth = this.targetWidth
              newHeight = this.targetHeight
            }
            break
          case 'width':
            newWidth = this.targetWidth
            newHeight = Math.round(imageData.height * (this.targetWidth / imageData.width))
            break
          case 'height':
            newHeight = this.targetHeight
            newWidth = Math.round(imageData.width * (this.targetHeight / imageData.height))
            break
        }

        canvas.width = newWidth
        canvas.height = newHeight
        
        ctx.drawImage(imageData.element, 0, 0, newWidth, newHeight)
        
        const format = this.outputFormat === 'original' ? 
          (imageData.file.type === 'image/png' ? 'image/png' : 'image/jpeg') :
          `image/${this.outputFormat}`
        
        const dataUrl = canvas.toDataURL(format, this.quality / 100)
        
        // è®¡ç®—æ–‡ä»¶å¤§å°
        const base64Data = dataUrl.split(',')[1]
        const size = Math.round((base64Data.length * 3) / 4)
        
        resolve({
          url: dataUrl,
          width: newWidth,
          height: newHeight,
          size: size,
          format: format
        })
      })
    },

    downloadSingle(index) {
      if (!this.processedImages[index]) return
      
      const link = document.createElement('a')
      const originalName = this.images[index].name
      const nameWithoutExt = originalName.substring(0, originalName.lastIndexOf('.'))
      const ext = this.getFileExtension(this.processedImages[index].format)
      
      link.download = `${nameWithoutExt}_resized.${ext}`
      link.href = this.processedImages[index].url
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },

    async downloadAll() {
      for (let i = 0; i < this.processedImages.length; i++) {
        if (this.processedImages[i]) {
          await new Promise(resolve => {
            setTimeout(() => {
              this.downloadSingle(i)
              resolve()
            }, 100 * i)
          })
        }
      }
      this.$message.success('å¼€å§‹ä¸‹è½½æ‰€æœ‰å¤„ç†åçš„å›¾ç‰‡ï¼')
    },

    getFileExtension(mimeType) {
      const extensions = {
        'image/jpeg': 'jpg',
        'image/png': 'png',
        'image/webp': 'webp'
      }
      return extensions[mimeType] || 'jpg'
    },

    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    getSizeChangeText(originalSize, newSize) {
      const change = ((newSize - originalSize) / originalSize * 100).toFixed(1)
      const changeText = change > 0 ? `+${change}%` : `${change}%`
      const color = change > 0 ? 'red' : 'green'
      return `<span style="color: ${color}">${changeText}</span>`
    }
  }
}
</script>

